.cellAction {
    display: flex;
    align-items: center;
    gap: 15px;
  
    .add {
      padding: 2px 5px;
      border-radius: 5px;
      color: green;
      border: 1px dotted green;
      cursor: pointer;
  
      &:hover {
        color: white;
        background-color: green;
      }
    }

    .viewButton {
        padding: 2px 5px;
        border-radius: 5px;
        color: darkblue;
        border: 1px dotted rgba(0, 0, 139, 0.596);
        cursor: pointer;
    
        &:hover {
          color: white;
          background-color: darkblue;
        }
      }
  
    .deleteButton {
      padding: 2px 5px;
      border-radius: 5px;
      color: crimson;
      border: 1px dotted rgba(220, 20, 60, 0.6);
      cursor: pointer;
  
      &:hover {
        color: white;
        background-color: crimson;
      }
    }
  }
  
  hr {
    margin: 1rem 3rem;
    background-color: #a693ea;
    outline: none;
  }
  