.cellAction {
  display: flex;
  align-items: center;
  gap: 15px;

  .viewButton {
    padding: 2px 5px;
    border-radius: 5px;
    color: darkblue;
    border: 1px dotted rgba(0, 0, 139, 0.596);
    cursor: pointer;

    &:hover {
      color: white;
      background-color: darkblue;
    }
  }

  .deleteButton {
    padding: 2px 5px;
    border-radius: 5px;
    color: crimson;
    border: 1px dotted rgba(220, 20, 60, 0.6);
    cursor: pointer;

    &:hover {
      color: white;
      background-color: crimson;
    }
  }
}

.addYear {
  padding: 1.5rem;
  flex: 1 1 auto;

  .title {
    width: 100%;
    font-size: 24px;
    color: gray;
    margin-bottom: 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    .saveChanges {
      text-decoration: none;
      color: green;
      font-size: 16px;
      font-weight: 400;
      border: 1px solid green;
      padding: 5px;
      border-radius: 5px;
      cursor: pointer;

      &.shake:hover {
        animation: shake 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97);
      }

      @keyframes shake {
        10%,
        90% {
          transform: translate3d(-1px, 0, 0);
        }
        20%,
        80% {
          transform: translate3d(2px, 0, 0);
        }
        30%,
        50%,
        70% {
          transform: translate3d(-4px, 0, 0);
        }
        40%,
        60% {
          transform: translate3d(4px, 0, 0);
        }
      }

      &:hover {
        background-color: green;
        color: white;
      }
    }
  }

  .heading {
    margin-top: 0;
    line-height: 1.5;
    font-size: 0.8rem;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: #8898aa !important;
    margin-bottom: 1.5rem !important;
  }

  .info {
    padding-left: 1.5rem !important;
  }

  .row {
    display: flex;
    margin-right: -15px;
    margin-left: -15px;
    flex-wrap: wrap;
  }

  .col {
    min-height: 1px;
    padding-right: 15px;
    padding-left: 15px;
    max-width: 50%;
    flex: 0 0 46%;

    .form-group {
      display: flex;
      margin-bottom: 0;
      flex: 0 0 auto;
      flex-flow: row wrap;
      align-items: center;
      margin-bottom: 1.5rem;

      label {
        margin-bottom: 0.5rem;
        font-size: 0.875rem;
        font-weight: 600;
        color: #525f7f;
      }

      input {
        font-family: inherit;
        margin: 0;
        overflow: visible;
      }

      .error {
        color: red;
        padding-top: 0.4rem;
      }

      .search-options {
        width: 100%;
      }

      .search-options input {
        height: 2rem;
        font-size: 1rem;
        line-height: 1.5;
        padding: 0.625rem 0.75rem;
        transition: all 0.2s cubic-bezier(0.68, -0.55, 0.265, 1.55);
      }

      .form-control {
        font-size: 1rem;
        line-height: 1.5;
        width: 100%;
        height: 1.5rem;
        padding: 0.625rem 0.75rem;
        transition: all 0.2s cubic-bezier(0.68, -0.55, 0.265, 1.55);
        border: 1px solid #cad1d7;
        border-radius: 0.375rem;
        background-color: #fff;
        background-clip: padding-box;
        box-shadow: none;
        outline: none;

        &:focus {
          border: 2px solid #2684ff;
        }
      }
    }
  }

  .title-divider {
    width: 28%;
    margin-left: 0;
    margin-bottom: 1.5rem;
  }

  .divider {
    margin-top: 1.5rem !important;
    margin-bottom: 1.5rem !important;
  }
}
